import React, { Component } from 'react';
import './App.css';
import Category from './components/Category';
import {data} from './data/categories.js';
class App extends Component {
  
  render() {
    return ( 
      <div className = "App" >
        {data.map(function(category,key){
          return (
        <Category category={category} key={key}/>
        )})}
      </div>
    );
  }
}

export default App;